<script>
import { turpial } from '../../libraries/turpial.js';
import StripeCard from './../../components/Elements/StripeCard.svelte';
import InternationalNumberInput from './../../components/Elements/InternationalNumberInput.svelte';
import { random_string } from '../../libraries/random_string.js';

import { StoreForm } from './StoreForm.js';
import { AppForms } from '../../libraries/AppForms.js';
export let changeStep;
export let Data;
export let errors;
let stripe_key;
if(Data.is_online === true){
    stripe_key = Data.stripeTokens.live;
}else{
    stripe_key = Data.stripeTokens.test;
}
let lang = Data.lang;
let formLang = lang.forms.planSelection;
let selectedPrice;
let buttonSubmit;
let termsLink = "https://mi3pod.com/terminosycondiciones";
let form = null;
let IS_ONLINE =  tripod_register.is_online ?? false;
let couponElement;

let plans;
let offPrice = 0;
function parsePlans(coupon){
    coupon = coupon ?? {};
    errors.hide();

    plans = $StoreForm.plansFetched.filter(( plan )=>{
        let price_month = plan.price_plan;
        let interval_year_price = plan.interval_year_price ?? plan.price_plan;
        let percent_off = 100-(interval_year_price*100)/price_month;
        offPrice = (price_month-interval_year_price)*12;
        plan.prices = [];
        let yearChecked =  false;
        if(percent_off !== 0){
            yearChecked = true
            selectedPrice = interval_year_price; // price_interval

            if(typeof coupon.percent_off !== "undefined"){
                interval_year_price = interval_year_price-((coupon.percent_off/100)*interval_year_price)
            }

            plan.prices.push({
                type: "year",
                name_plan: plan.name_plan,
                price_month: interval_year_price,
                price_interval: interval_year_price,
                real: interval_year_price,
                yearly: interval_year_price*12,
                id: "price-plan-"+random_string(6),
                checked: "checked='checked'",
            })
        }

        if(typeof coupon.percent_off !== "undefined"){
            price_month = price_month-((coupon.percent_off/100)*price_month)
        }


        plan.prices.push({
            type: "month",
            name_plan: plan.name_plan,
            price_month: price_month,
            price_interval: price_month,
            real: price_month,
            yearly: price_month*12,
            id: "price-plan-"+random_string(6),
            checked: yearChecked === false ? "checked='checked'" : "",
        })    
        return plan;
    })
}

parsePlans();

function updateDataForm(){
    // update any error
    errors.hide();

    let data = AppForms.retrieve({
        form: form,
        targets: ".formElements",
    })
    StoreForm.update("dataForm", data, $StoreForm.dataForm);
    data = $StoreForm.dataForm;
    return data;
}

let hasErrorForms = false;
function checkForm(data){
    let is_all_valid = true;
    data = data || {};

    // checks validation inputs.
    let phone = form.querySelector(".int-phone");
    if(phone.dataset.valid === "false"){ is_all_valid = false }
    if(couponElement.dataset.valid === "false"){ is_all_valid = false; }


    // check empty inputs
    let checks = ["first-name", "last-name", "email", "password", "phone", "city", "address", "zip", "card_name_owner"];
    checks.map(( element )=>{
        if(typeof data[element] !== "string" && data[element] === ""){
            is_all_valid = false;
        }
    });

    
    
    return is_all_valid;
}

let stripeToken = "";
function submitPayment(){
    let data = updateDataForm();
    
    if(checkForm(data) === false){ 
        AppForms.statusButton(buttonSubmit, "enabled");
        errors.showError(lang.errors.uncompleted);
        return;
    }
    AppForms.statusButton(buttonSubmit, "disabled");
    tripod_register.stripe.module.createToken(tripod_register.stripe.card).then(function(result) {
        if (result.error) {
            // Inform the user if there was an error.
            tripod_register.stripe.cardErrorElement.textContent = result.error.message;
            AppForms.statusButton(buttonSubmit, "enabled");
        } else {
            // Send the token to your server.
            // send data to server            
            data.stripeToken = result?.token?.id||"none";
            data.userFromCampaign = tripod_register.userFromCampaign||"widget-registration";

            let url;
            if(IS_ONLINE === false){
                url = "http://localhost/app.3pod.io/public/api/v1/create_customer_subscription";
            }else{
                url = "https://app.3pod.io/public/api/v1/create_customer_subscription";
            }

            turpial.fetch({
                url: [url],
                data: JSON.stringify(data),
                method: "POST",
                type: "text",
                ready: (data)=>{                    
                    data = JSON.parse(data[0]);
                    let message = data?.message ?? "";
                    if(data?.status === "error"){
                        errors.showError(message);
                        AppForms.statusButton(buttonSubmit, "enabled");
                        return;
                    }

                    let redirect = data?.response?.url ?? null; 
                    if(redirect === null){
                        errors.showError(message);
                        return;
                    }
                    setTimeout(() => {
                        window.location.href = redirect;
                    }, 450);
                },
                onerror: (data)=>{
                    console.error(data)
                    AppForms.statusButton(buttonSubmit, "enabled");
                }
            })

        }
    });
}
function checkCoupon(event){
    let input = event.target;
    if(input.value === ""){
        input.classList.remove("coupon-success");
        input.classList.remove("coupon-error");
        input.dataset.valid = true;
        return;
    }

    if(input.value.length <= 3){
        input.classList.add("coupon-error");
        input.classList.remove("coupon-success");
        input.dataset.valid = false;
        parsePlans();
        return;
    }

    let url;
    if(IS_ONLINE === false){
        url = "http://localhost/app.3pod.io/public/api/v1/getCoupon?coupon="+input.value;
    }else{
        url = "https://app.3pod.io/public/api/v1/getCoupon";
    }


    turpial.fetch({
        url: [url],
        method: "POST",
        type: "text",
        ready: (data)=>{
            data = JSON.parse(data[0]);
            if(data?.status === "success"){
                input.classList.add("coupon-success");
                input.classList.remove("coupon-error");
                input.dataset.valid = true;

                parsePlans(data.response)

            }else{
                input.classList.add("coupon-error");
                input.classList.remove("coupon-success");
                input.dataset.valid = false;
                parsePlans()
            }
        }
    })
}
</script>
<header>
    <div class="navigation">
        <button class="button button-success-3pod is-success is-rounded button-navigation" on:click={changeStep("BasicForm")}>
            <svg width="100%" height="100%" version="1.1" viewBox="0 0 81.08 138.25" xmlns="http://www.w3.org/2000/svg">
                <g transform="translate(-28.719 -66.543)">
                    <path d="m100.23 204.33c1.715-0.59282 2.8475-1.4842 5.8714-4.6215 3.0866-3.2024 3.8141-4.685 3.6372-7.4122-0.0688-1.0609-0.33876-2.3545-0.59985-2.8747-0.2611-0.52016-12.349-12.831-26.861-27.358l-26.386-26.413 26.264-26.279c28.686-28.702 27.633-27.534 27.638-30.66 5e-3 -2.7306-0.70166-3.9192-4.6569-7.8321-3.2965-3.2613-3.7094-3.5805-5.1808-4.0067-1.8854-0.54601-3.6109-0.41712-5.494 0.41038-1.0502 0.46149-7.0217 6.3023-32.819 32.101-34.886 34.887-32.905 32.705-32.905 36.265 0 1.2934 0.16995 2.1125 0.64468 3.1074 0.52123 1.0924 6.7682 7.4651 32.626 33.282 31.227 31.178 32.015 31.941 33.449 32.358 1.8723 0.5445 3.0525 0.52797 4.7733-0.0668z" stroke-width=".27021"/>
                </g>
            </svg>
        </button>
    </div>
    <h2 class="has-text-primary fw800 is-size-3">
        {formLang.title}
    </h2>
    <p class="has-text-dark f200">
        {@html formLang.subtitle}
    </p>
    <div class="progress-bar">
        <p class="steps-texts has-text-white">
            Paso 2/2
        </p>
        <progress class="progress is-primary is-large" value="100" max="100">100%</progress>
    </div>
</header>
<main>
    <form action="" on:input|preventDefault="{ updateDataForm }" on:submit|preventDefault="{ submitPayment }" bind:this="{form}">
        <input type="hidden" name="stripeToken" value={stripeToken}>
        <input type="hidden" name="card_name_owner" value={stripeToken}>
        {#each plans as plan}
            <div class="columns is-multiline is-centered" style="column-gap:1rem;" data-plan="{plan.name_plan}">
                {#each plan.prices as price_plan}
                        <div class="box-plan column is-5 {price_plan.type}">
                            {#if price_plan.type === "year"}
                                {#if offPrice > 0}
                                    <div class="headband-promo">
                                        <p class="has-text-centered fw600">
                                            {formLang.headband} {Data.currency[0]}{offPrice} 
                                            <span class="fw400" style="font-size:8.5px;">
                                                {Data.currency[1]}
                                            </span>
                                        </p>
                                    </div>
                                {/if}
                                <div class="buttons is-grouped">
                                    <span class="tag tag-percent is-rounded">
                                        -20%
                                    </span>
                                </div>
                            {/if}
                            <div class="container-box">
                                <div>
                                    <h2 class="is-size-4 fw800">
                                        Plan Funnel del Dinero
                                    </h2>
                                    <br>
                                    <p class="has-text-centered is-size-5">
                                        Plan <span class="is-capitalized">
                                            {formLang[price_plan.type]}
                                        </span>
                                    </p>                    
                                    <div>
                                        <span class="is-size-7">
                                            {Data.currency[0]}
                                        </span>
                                        <span class="is-size-4 fw800">
                                            {price_plan.price_month}
                                        </span>
                                        <span class="is-size-7">
                                            {Data.currency[1]}/{formLang.intervals2.month}
                                        </span>
                                        <br>
                                        <span style="font-size:12px;">
                                            ({Data.currency[0]}
                                            {price_plan.yearly} {Data.currency[1]}/{formLang.intervals2.year})
                                        </span>
                                    </div>
                                    <br>
                                    <div>
                                        <input type="hidden" class="formElements" name="id_plan" value="{plan.__id__}">
                                        <input type="hidden" class="formElements" name="membership_type" value="{plan.membership_type}">
                                    </div>
                                    <div class="field field-check-plan">
                                        <input data-plan={plan.__id__} class="is-checkradio is-rtl is-circle is-warning formElements" id="{price_plan.id}" value="{price_plan.type}" type="radio" data-price="{price_plan.price_interval}" on:change={(e)=>{ selectedPrice = e.target.dataset.price; }} checked={price_plan.checked||""} name="interval">
                                        <label for="{price_plan.id}"></label>
                                    </div>
                                </div>                               
                            </div>
                        </div>            
                {/each}
            </div>
        {/each}
        <div class="columns is-multiline" style="padding:1.45rem;">
            <div class="field column is-6">
                <div class="control has-text-left">
                    <label for="" class="fw600 label-text">{formLang.phone}</label>
                    <InternationalNumberInput 
                        selected_code={$StoreForm.dataForm?.code||"CO"}
                        selected_dial={$StoreForm.dataForm?.dial||"+57"}
                        selected_country={$StoreForm.dataForm?.country||"Colombia"}
                        selected_value={$StoreForm.dataForm?.phone||""}
                        ClassInputs="formElements int-phone" />
                </div>
            </div>
            <div class="field column is-6">
                <div class="control has-text-left">
                    <label for="" class="fw600 label-text">{formLang.city}</label>
                    <input type="text" value="{$StoreForm?.dataForm["city"] ?? ""}" name="city" placeholder="{formLang.city}" class="input formElements is-rounded is-hovered" minlength="4" required>
                </div>
            </div>
            <div class="field column is-6">
                <div class="control has-text-left">
                    <label for="" class="fw600 label-text">{formLang.address}</label>
                    <input type="text" value="{$StoreForm?.dataForm["address"] ?? ""}" name="address" placeholder="{formLang.address}" class="input formElements is-rounded is-hovered" minlength="4" required>
                </div>
            </div>
            <div class="field column is-6">
                <div class="control has-text-left">
                    <label for="" class="fw600 label-text">{formLang.zip}</label>
                    <input type="text" value="{$StoreForm?.dataForm["zip"] ?? ""}" name="zip" placeholder="{formLang.zip}" class="input formElements is-rounded is-hovered" minlength="4" required>
                </div>
            </div>            
            <div class="field column is-12">
                <div class="control has-text-left">
                    <label for="" class="fw600 label-text">{formLang.card_name_owner}</label>
                    <input type="text" value="{$StoreForm?.dataForm["card_name_owner"] ?? ""}" name="card_name_owner" placeholder="{formLang.card_name_owner}" class="input formElements is-rounded is-hovered" minlength="4" required>
                </div>
            </div>
            <div class="field column is-8">
                <div class="control has-text-left">
                    <label for="" class="fw600 label-text">{formLang.card}</label>
                    <StripeCard public_key={stripe_key} />                    
                </div>
            </div>
            <div class="field column is-4">
                <div class="control has-text-left">
                    <label for="" class="fw600 label-text">{formLang.coupon}</label>
                    <input bind:this="{couponElement}" on:input={checkCoupon} type="text" value="{$StoreForm?.dataForm["coupon"] ?? ""}" name="coupon" placeholder="{formLang.coupon}" class="input formElements is-rounded is-hovered coupon" minlength="4">
                </div>
            </div>
            <div class="field column is-6 has-text-left">
                <div class="control cards">
                    <span>
                        <img class="card-images" src="https://app.3pod.io/default/public/assets/_images_assets_/gallery/public/CUgaUF.png" alt="Credit/Debit Cards">
                    </span>
                    <span>
                        <img class="card-images" src="https://app.3pod.io/default/public/assets/_images_assets_/gallery/public/D2JUAU.png" alt="Credit/Debit Cards">
                    </span>
                </div>
            </div>
            <div class="field column is-6 has-text-left">
                <div class="control cards">
                    <div class="is-flex">
                        <span>
                            <img class="card-images" src="https://i.ibb.co/SJcrt9Q/variante-de-escudo-con-bordes-blancos-y-negros-2x.png" alt="Pago seguro SSL">
                        </span>
                        
                        <span class="is-size-7">
                            &nbsp;Pago seguro SSL
                        </span>
                    </div>
                </div>
            </div>
            <div class="field column is-12">
                <div class="control">
                    <div class="field">
                        <input class="is-checkradio is-circle is-success is-small" id="agree-terms-3pod" value="true" type="checkbox" name="agree-terms" required>
                        <label for="agree-terms-3pod" class="is-pulled-left">
                            <p class="has-text-left is-size-7">
                                {#each (lang?.forms?.guarantee ?? []) as item}
                                    {@html item}
                                {/each}
                            </p>
                        </label>
                    </div>
                </div>
            </div>
            <div class="field column is-12">
                <div class="buttons is-grouped is-centered">
                    <button bind:this={buttonSubmit} type="submit" class="button is-rounded is-success has-text-white">
                        {formLang.button}
                    </button>
                </div>
            </div>
        </div>
    </form>
</main>
<style>
main{
    margin-top: 1.25rem;
    padding-top: 1.25rem;
}
.coupon:global(.coupon-success), .coupon:global(.coupon-success:focus){
    border: 1px solid var(--color-green);
}
.coupon:global(.coupon-error), .coupon:global(.coupon-error:focus){
    border: 1px solid var(--color-red);
}
.card-images{
    max-height: 20px;
}
progress[value]::-webkit-progress-value{
    background: transparent linear-gradient(90deg, #344FFF 0%, #09E2FF 100%) 0% 0% no-repeat padding-box;
}
.progress-bar{
    margin-top: 1.25rem !important;
    padding: 1rem 2rem;
    position: relative;
    width: 75%;
    margin: auto;
}
.steps-texts{
    position: absolute;
    width: 100%;
    text-align: center;
    top: 1rem;
    left: 0.75rem;
}
.box-plan{
    position: relative;
    padding-top: 2.75rem;
    padding: 2rem;
    min-height: 200px;    
    border: 1px solid var(--color-primary);
    border-radius: 20px;
    overflow: clip;
}
.box-plan .container-box{
    display: flex;
    align-items: center;
}
.box-plan h2{
    letter-spacing: 0px;
    line-height: 100%;
}
.tag-percent{
    background: transparent;
    border-radius: 20px;
    border: 1px solid var(--color-primary);
    padding: 0.75rem 1.25rem;
}
.button-navigation{
    padding: 9px 13px;
    position: absolute;
    top: -1.75rem;
    left: 0.45rem;
}
.button-navigation svg path{
    fill: #fff;
}
header{
    position: relative;
    margin-bottom: 1.25rem;
}
.field-check-plan{
    position: absolute;
    margin: auto;
    width: calc(100% - 5.25rem);
    bottom: 1rem;
}
.box-plan.year{
    background: transparent linear-gradient(180deg, #344fff 0%, #09E2FF 100%) 0% 0% no-repeat padding-box;;
}
.box-plan.year .tag-percent{
    color: #fff;
    border: 1px solid #fff;
}
.box-plan.year .container-box{
    color: #fff;
}
.headband-promo{
    position: absolute;
    top: 29px;
    right: -67px;
    transform: rotate(45deg);
    background: var(--color-yellow);
    color: var(--color-info);
    font-size: 11px;
    padding: 4px 4rem;
}
</style>