<script>
import { StoreForm } from './StoreForm.js';
import { AppForms } from "../../libraries/AppForms";
import { turpial } from "../../libraries/turpial";


export let changeStep;
export let Data;
export let errors;



let lang = Data.lang;
let buttonSubmit = null;
let formLang = lang.forms.basic;



function catchData(event){
    errors.hide();
    let form = event.target;
    let data = AppForms.retrieve({
        targets: ".formElements",
        form: form,
    });
    data.password_again = data.password;
    let dataForm = data;
    let encodeEmail = btoa(data.email).split("").reverse().join("")
    AppForms.statusButton(buttonSubmit, "disabled");
    turpial.fetch({
        files: [
            "https://app.3pod.io/api/getPlans",
            "https://app.3pod.io/api/isUserRegistered/"+encodeEmail
        ],
        method: "GET",
        type: "text",
        ready: (data)=>{
            let plans = JSON.parse(data[0]).response[tripod_register.membership];
            let isEmailRegistered = JSON.parse(data[1]);
            AppForms.statusButton(buttonSubmit, "enabled");
            if(isEmailRegistered.response === true){
                errors.showError(lang.errors.email_registered);
                return;
            }
            let plans_selected = plans.filter(( plan )=>{
                // buscar entre los seleccionados en tripod_register
                if(typeof tripod_register.plans_selected.find(( selected )=>{
                    if(selected === plan.__id__){
                        return selected;
                    }
                }) !== "undefined"){
                    // si se encuentran coicidencias devolver el plan


                    
                    // PRUEBA DE DATOS!!!
                    plan.interval_year_price = 20;
                    ////////////////////******/


                    return plan;
                }
            })

            tripod_register.plans = plans;
            tripod_register.dataForm = dataForm;            

            tripod_register.plansFetched = plans_selected;

            StoreForm.update("plansFetched", plans_selected);
            StoreForm.update("plans", plans);
            StoreForm.update("dataForm", dataForm, $StoreForm?.dataForm);


            console.log($StoreForm);
            changeStep("PaymentFunnels");
        },
        onerror: (data)=>{
            console.log.error(data)
            AppForms.statusButton(buttonSubmit, "enabled");
        }
    });
}
let pattern = ".{6,}"
</script>
<style>
form{
    position: relative;
    padding: 1.75rem 2.25rem;
}
form .field{
    padding: 0.5rem 0.45rem;
}
form .field .control{
    text-align: left;
}
</style>
<header>
    <h2 class="has-text-primary fw800 is-size-3">
        {lang.title}
    </h2>
    <p class="has-text-dark f200">
        {@html lang.subtitle}
    </p>                
</header>
<main>
    <form class="columns is-multiline" action="" method="POST" on:submit|preventDefault="{(e)=>{ catchData(e) }}">
        <div class="field column is-6">
            <div class="control">
                <label for="" class="fw600 label-text">{formLang.name} <span class="has-text-danger">*</span></label>
                <input type="text" value="{$StoreForm?.dataForm["first-name"] ?? ""}" name="first-name" placeholder="{formLang.name}" class="input formElements is-rounded is-hovered" minlength="4" required>
            </div>
        </div>
        <div class="field column is-6">
            <div class="control">
                <label for="" class="fw600 label-text">{formLang.lastname} <span class="has-text-danger">*</span></label>
                <input type="text" name="last-name" value="{$StoreForm?.dataForm["last-name"] ?? ""}" placeholder="{formLang.lastname}" class="input formElements is-rounded is-hovered" minlength="4" required>
            </div>
        </div>
        <div class="field column is-12">
            <div class="control">
                <label for="" class="fw600 label-text">{formLang.email} <span class="has-text-danger">*</span></label>
                <input type="email" name="email" value="{$StoreForm?.dataForm["email"] ?? ""}" placeholder="E-mail" class="input formElements is-rounded is-hovered" minlength="4" required>
            </div>
        </div>
        <div class="field column is-12">
            <div class="control">
                <label for="" class="fw600 label-text">{formLang.password} <span class="has-text-danger">*</span></label>
                <input pattern={pattern} title="{formLang.errors.password_error}" type="password" value="{$StoreForm?.dataForm["password"] ?? ""}" name="password" placeholder="Contraseña" class="input formElements is-rounded is-hovered" required>
            </div>
        </div>
        <div class="field column is-12">
            <div class="buttons is-grouped is-centered">
                <button bind:this={buttonSubmit} type="submit" class="button is-rounded is-success has-text-white">
                    {formLang.button}
                </button>
            </div>
        </div>
        {#if formLang?.sign_in?.active ?? true === true}
            <div class="field column is-12">
                <p>
                    {formLang.sign_in.already_has} <span class="fw600 has-text-dark">
                        <a href="#/" class="has-text-dark is-underlined">
                            {formLang.sign_in.in}
                        </a>
                    </span>
                </p>
            </div>
        {/if}
    </form>

</main>